.modal {

  /* background-color: #63bd14 0.4; */

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
   display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
  /*font-size: 35;*/
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal__content {
font-size: 35px;
  /*padding: 40px;*/
  /*border-radius: 12px;*/
  background-color: rgb(236, 134, 134);
  /*height: 200px;*/
  /*width: 200px;*/
  padding: 50px;
  border-radius: 50px;

    border: 10px solid rgb(239,245,195);




  /* background: rgb(222,231,178); */
  opacity: .3;


  height: 400px;
  width: 400px;

  transform: scale(0.5);
  transition: 0.4s all;
}

 .modal_content.active {
  transform: scale(1);
}



